import React, { Component } from 'react';
import InfiniteScroll from 'react-infinite-scroll-component';
import HadithCard from "./HadithCard";
import resourcePath from "../configuration";
import Container from 'react-bootstrap/Container';
import Spinner from 'react-bootstrap/Spinner';
import Col from 'react-bootstrap/Col';

const FETCH_STRIDE = 10;
const spinnerStyle = {
    position: 'absolute', left: '50%', top: '50%',
};

class Timeline extends Component {

    constructor(props) {
        super(props);

        this.state = {
            cardIndex: 0,
            cards: [],
            reachedEnd: false,
            loading: false
        };
    }

    componentDidMount() {
        this.getNextData();
    }

    mdUrl = (index) => `${resourcePath}/${index}/${index}_text.md`;
    refMdUrl = (index) => `${resourcePath}/${index}/${index}_ref.md`;
    gifUrl = (index) => `${resourcePath}/${index}/v_${index}.webm`;

    fetchMarkdown = async (url) => fetch(url)
        .then((res) => {
            /* If there is no data to fetch remaining */
            if (!res.ok) { throw new Error("Not 2xx response" + JSON.stringify(res)); }
            else { return res.text(); }
        });

    videoUrlExists = async (url) => fetch(url, {
        method: 'HEAD'
    }).then((res) => {
        return res.ok;
    }).catch(() => false);

    makeCard = ({ markdown, refMarkdown, gifExists, gifUrl }, index) => {
        let srcGifPath = gifExists ? gifUrl : null;
        return <HadithCard gifPath={srcGifPath} data={markdown} refText={refMarkdown} index={index} key={index} />
    };

    loadHadith = async (index, markdownUrl, refMarkdownUrl, gifUrl) => {
        let markdown = await this.fetchMarkdown(markdownUrl);
        let refMarkdown = await this.fetchMarkdown(refMarkdownUrl);
        let gifExists = await this.videoUrlExists(gifUrl);

        this.setState((state) => {
            let card = this.makeCard({
                markdown,
                refMarkdown,
                gifExists,
                gifUrl
            }, index);

            let cards = state.cards.concat(card);

            // Sort cards after fetching to allow parallel fetches
            // to display sorted results.
            cards.sort((a, b) => {
                if (a.props.index < b.props.index) return -1;
                return 1;
            })
            return { cards };
        });
    }

    getNextData = async () => {
        if (this.state.reachedEnd) return;

        let start = this.state.cardIndex;
        let end = start + FETCH_STRIDE;
        let dataCount = 0;

        this.setState(() => ({
            loading: true
        }));

        for (let i = start; i < end; i++) {
            let index = i + 1;
            let markdownUrl = this.mdUrl(index);
            let refMarkdownUrl = this.refMdUrl(index);
            let gifUrl = this.gifUrl(index);
            /* Try to get the data if there is remaining */
            try {
                this.loadHadith(index, markdownUrl, refMarkdownUrl, gifUrl);
                dataCount++;
            }
            catch (e) {
                // TODO: cleanup
                // TODO: Use the index.json object to
                // know the end of data.
                /* The data fully loaded */
                await this.setState({
                    reachedEnd: true
                });

                break;
            }
        }

        await this.setState({
            cardIndex: this.state.cardIndex + dataCount,
            loading: false
        });
    };

    render() {
        /* If data is not loaded */
        if (this.state.cards.length === 0) {
            return (
                <Container>
                    <Spinner animation="grow" role="status" style={spinnerStyle}>
                        <span className="sr-only">Loading...</span>
                    </Spinner>
                </Container>
            );
        }

        return (
            <Container>
                <Col>
                    <InfiniteScroll
                        dataLength={this.state.cards.length}
                        next={this.getNextData}
                        hasMore={!this.state.loading && !this.state.reachedEnd}
                        loader={this.state.cards.length < this.state.size ? <h4>Loading...</h4> : <div />}
                    >
                        {this.state.cards}
                    </InfiniteScroll>
                </Col>
            </Container>
        );
    }
}

export default Timeline;
